.Home__header {
    display: block;
    text-align: center;
    padding: 20px 0 20px 0;
}

.Home__main {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.Home__footer {
    padding: 10px 0 10px 10px;
    border-top: 1px solid black;
    width: 100%;
    -ms-flex-item-align: end;
        align-self: flex-end;
}

@media all and (min-width: 798px) {
    .Home__header {
        font-size: 35px;
    }
}



